
import { Typography, div, IconButton } from "@material-ui/core";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import humidity from "../images/humidity.svg";
import pressure from "../images/pressure.svg";

const Item = ({ data, key, degUnit }) => (
    <div class='bg-green-800 text-white flex rounded-lg p-3 border-2 flex flex-col' style={{ minWidth: '20rem' }}>
        <div id='header' class='flex flex-grow justify-between'>
            <div id='location-date' class='w-6/12'>
                <Typography variant='p' className='font-medium' sx={{ color: 'error.light' }}>
                    {data.date}
                </Typography>
                <Typography variant='p' className='block text-3xl font-medium'>
                    {data.day}
                </Typography>
            </div>

            <div id='temp' class='w-5/12 text-center self-center'>
                <Typography variant='p' className='block text-4xl font-medium' sx={{ color: 'warning.light' }}>
                    {degUnit === 'C' ? data.dayTemp : (parseInt(data.dayTemp) * 9 / 5 + 32)}°{degUnit}</Typography>
            </div>

            <div id='delete-button' className='w-1/12'>
                {/* <IconButton id="long-button" onClick={""}>
                    <MoreVertIcon className='text-white hover:text-green-600' />
                </IconButton> */}
            </div>
        </div>

        <div id='content'>
            <div class='flex mb-3 mt-2'>
                <div id='weather-status' class='w-5/12 bg-red-90 flex flex-col items-center justify-start'>
                    <img width='80' className='block' src={data.icon} alt='weather' style={{ marginTop: '-20px' }} />
                    <Typography variant='p' className='text-2xl' sx={{ marginTop: '-15px' }}>{data.weatherStatus}</Typography>
                </div>

                <div className='w-7/12 flex flex-col space-y-2 mt-1 font-medium'>
                    <div className='flex justify-around'>
                        <div>Min: {degUnit === 'C' ? data.minTemp : (parseInt(data.minTemp) * 9 / 5 + 32)}°{degUnit}</div>
                        <div>Max: {degUnit === 'C' ? data.maxTemp : (parseInt(data.maxTemp) * 9 / 5 + 32)}°{degUnit}</div>
                    </div>
                    <div className='flex justify-around'>
                        <div>Day: {degUnit === 'C' ? data.dayTemp : (parseInt(data.dayTemp) * 9 / 5 + 32)}°{degUnit}</div>
                        <div>Night {degUnit === 'C' ? data.nightTemp : (parseInt(data.nightTemp) * 9 / 5 + 32)}°{degUnit}</div>
                    </div>
                    <div className='flex justify-around'>
                        <div>Morn: {degUnit === 'C' ? data.mornTemp : (parseInt(data.mornTemp) * 9 / 5 + 32)}°{degUnit}</div>
                        <div>Eve: {degUnit === 'C' ? data.eveTemp : (parseInt(data.eveTemp) * 9 / 5 + 32)}°{degUnit}</div>
                    </div>


                </div>
            </div>
            <div className='flex flex-wrap space-x-2 justify-center items-center' style={{ width: '20rem' }}>
                <Typography class='block flex items-center'><img width='23' className='inline-block mr-1' alt='' src={pressure} /> Pressure : <span style={{ color: 'yellow' }}>{data.pressure} hPa</span></Typography>
                <Typography class='block'><img width='25' className='inline-block' alt='' src={humidity} />Humidity : <span style={{ color: 'yellow' }}>{data.humidity}%</span></Typography>
                <Typography class='block'> <i class="fas fa-wind" /> WindSpeed : <span style={{ color: 'yellow' }}>{data.windSpeed} m/s</span></Typography>
            </div>
        </div>
    </div>
)

function Forecast({ degUnit, data }) {
    return (
        <div className='w-screen mt-8 p-4 text-white flex flex-col'>
            {data.title && <Typography variant='p' className='self-start text-2xl border-2  border-yellow-200
                font-medium  bg-black p-2 rounded-lg text-blue-400  '>Forecast For {data.title}</Typography>}

            <div className='flex justify-start overflow-x-auto space-x-3 mt-4 mb-28'>
                {data.list && data.list.map((data, key) => <Item data={data} key={key} degUnit={degUnit} />)}
            </div>

        </div>
    )
}


export default Forecast;