
import { AppBar, Typography, TextField, InputAdornment, Button, div, Icon, Hidden } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';
import brandImg from "../images/brandImg.svg";
import { useState, useEffect, useRef } from 'react';
import { Autocomplete } from "@material-ui/core";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import moment from 'moment';


const useCollapseButton = () => {
    const [collapseButton, setCollapseButton] = useState(false);
    const handleCollapseButton = () => setCollapseButton(!collapseButton);

    return [collapseButton, handleCollapseButton]
}

const Search = ({ collapseButton, handleOpen, size, only }) => {
    const [value, setValue] = useState(null);
    const [inputValue, setInputValue] = useState('');
    const [options, setOptions] = useState([]);

    const timeout = useRef(null);

    // fetch a googlePlaces query whenever the search input changes
    useEffect(() => {
        /* :debug 
        console.log(inputValue);
        console.log(value);
        */

        // do not fetch new googlePlaces queries when the user selects an option or when the input is an empty string
        if (inputValue && value !== inputValue) {
            let query = inputValue;
            clearTimeout(timeout.current);
            timeout.current = setTimeout(() => {
                const url = "https://muterehm3f.execute-api.us-east-2.amazonaws.com/official/apiweatherapp";
                const options = { "method": "POST", body: JSON.stringify({ target: 'googlePlaces', query: query }) }
                fetch(url, options)
                    .then(response => response.json())
                    .then(data => data.results.map(item => {
                        return { address: item.formatted_address, location: item.geometry.location, name: item.name }
                    }).slice(0, 10)) // get only top 10 result
                    .then(data => {
                        /* :debug */
                        console.log(data);
                        return data
                    })
                    .then(data => setOptions(options => data ? data : options))

            }, 200)
        }
    }, [inputValue, value])

    const submitRequest = () => {
        const formatCurrentData = (data) => {
            let hour = parseInt(moment.unix(data.current.dt).format("HH"));
            let minute = moment.unix(data.current.dt).format("mm");
            hour = hour > 12 ? `${hour - 12}:${minute}pm` : `${hour}:${minute}am`;
            let date = `${moment.unix(data.current.dt).format("MMM[,] Do,")} ${hour}`;
            data.current = { ...data.current, dt: date };
            return data;
        };

        const formatDailyData = (data) => {
            data.daily = data.daily.map(item => {
                let hour = parseInt(moment.unix(item.dt).format("HH"));
                let minute = moment.unix(item.dt).format("mm");
                hour = hour > 12 ? `${hour - 12}:${minute}pm` : `${hour}:${minute}am`;
                let date = `${moment.unix(item.dt).format("MMM[,] Do,")} ${hour}`;
                let day = moment.unix(item.dt).format("dddd");
                return { ...item, date:date, day:day };
            });
            return data;
        }

        if (value && inputValue && value === inputValue) {
            /* :debug 
            console.log("inputs match")
            console.log(value.location.lng);
            */
            const url = "https://muterehm3f.execute-api.us-east-2.amazonaws.com/official/apiweatherapp";
            let lon = value.location.lng;
            let lat = value.location.lat;
            const options = { "method": "POST", body: JSON.stringify({ target: 'openWeather', lon: lon, lat: lat }) }
            fetch(url, options)
                .then(response => response.json())
                .then(data => formatCurrentData(data))
                .then(data => formatDailyData(data))
                .then(data => console.log(data))
        }
        else {
            /* :debug
            console.log("inputs do not match")
            */
            toast.dark('⛔️ Invalid location provided!')
        }

    }
    return (

        <Hidden mediaQuery only={only}>
            <div class={`space-x-2 ${collapseButton ? 'hidden' : 'contents'}`} >

                <Autocomplete id='search' className={size}
                    getOptionLabel={(option) => option.address}
                    options={options}
                    filterOptions={(x) => x}
                    value={value}
                    onChange={(e, newValue) => {
                        setValue(newValue);
                        setInputValue(newValue);
                    }}
                    onInputChange={(e, newInputValue) => {
                        setInputValue(newInputValue);
                    }}
                    renderInput={(params) =>
                        <TextField {...params}
                            InputProps={{
                                ...params.InputProps,
                                size: 'small',
                                placeholder: 'Enter the Name of a Location',
                                margin: 'dense',
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <SearchIcon />
                                    </InputAdornment>
                                ),
                            }}
                            class={`w-full bg-white text-black rounded-xl `} />}
                />

                <div className='flex space-x-2 mb-2 sm:mb-0'>
                    <Button size='small' variant="outlined" className='h-10 lg:self-center' onClick={submitRequest}>Get Forecast</Button>
                    <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
                </div>
            </div>
        </Hidden>

    )
}


function NavBar({ handleOpen }) {
    const [collapseButton, handleCollapseButton] = useCollapseButton();
    return (

        <AppBar class='bg-black text-white p-2 py-3  w-full'>
            <div id='nav-row' class='flex'>
                <div class='flex space-x-1 items-center'>
                    <Icon fontSize='large'><img alt='brand visual' src={brandImg} /></Icon>
                    <Typography variant='h6'>API Weather App</Typography>
                </div>
                <div class='flex justify-center items-center flex-grow'>
                    <Search handleOpen={handleOpen} size='w-5/12 mx-2' only={['xs', 'sm']} />
                </div>
                <Hidden mdUp>
                    <div class='ml-auto self-center' component='button' onClick={handleCollapseButton} >
                        <MenuIcon color='white' fontSize='large' />
                    </div>
                </Hidden>
            </div>

            <div class='flex justify-center items-center flex-grow'>
                <Search handleOpen={handleOpen} collapseButton={collapseButton} size='w-5/12' only={['xs', 'lg', 'md', 'xl']} />
            </div>
            <div class='flex flex-col-reverse justify-center items-center flex-grow'>
                <Search handleOpen={handleOpen} collapseButton={collapseButton} size='w-11/12' only={['sm', 'lg', 'md', 'xl']} />
            </div>
            <ToastContainer oastContainer
                position="bottom-left"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={true}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss={false}
                draggable
                pauseOnHover className='flex flex-col items-start mb-2' />
        </AppBar>

    )
};


export default NavBar;