import React, { useState, useEffect} from 'react';
import NavBar from "./components/NavBar";
import cover from "./images/wallpapers/wallpaper10.jpg";
import Dashboard from "./components/Dashboard";
import Forecast from "./components/Forecast";
import Options from "./components/Options";
import { Container, div } from '@material-ui/core';
import sunny from "./images/weatherStates/sunny.svg";


const useDeg = () => {
  const [degUnit, setDegUnit] = useState('F');
  const handleDegF = () => setDegUnit('F');
  const handleDegC = () => setDegUnit('C');
  return [degUnit, handleDegC, handleDegF];
}

const useOptions = () => {
  const [open, setOpen] = useState(false);
  const [wallpaper, setWallpaper] = useState(cover);
  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);
  return [open, handleOpen, handleClose, wallpaper, setWallpaper];
}

const useForecastList = () => {
  const [forecastList, setForecastList] = useState(getData());
  const [selectedForecast, setSelectedForecast] = useState(forecastList[0]? { title: forecastList[0].city, list: forecastList[0].daily }: {});
  const handleSelectedForecast = (index) => setSelectedForecast(forecastList[index] ? { title: forecastList[index].city, list: forecastList[index].daily } : {});
  const handleDeleteForecast = (index) => {
    let list = forecastList;
    list.splice(0, 1);
  }

  useEffect(() => {
    setForecastList(forecastList);
  }, [handleDeleteForecast])

  return { forecastList: forecastList, selectedForecast: selectedForecast, setSelectedForecast: handleSelectedForecast, setForecastList: setForecastList, deleteForecast:handleDeleteForecast };
}



const getData = () =>  [
  {
    city: '8417 Spruce Hill Dr',
    lat: '',
    lon: '',
    key: `${1234}${5678}`,
    current: {
      day: 'Monday',
      date: 'July, 6th, 08:37am',
      timezone:  'America/NewYork',
      sunrise: '08:00am',
      sunset: '07:00pm',
      temp: '0',
      icon: sunny,
      weatherStatus: 'Sunny',
      shortDescription: 'Light rain, gentle breeze.',
      humidity: '100',
      pressure: '100',
      windSpeed: '200',
      visibility: '300'
    },
    daily: [
      {
        date: 'July, 6th, 08:37am',
        day: 'Monday',
        description: 'light rain',
        icon: sunny,
        weatherStatus: 'Sunny',
        humidity: '100',
        windSpeed: '200',
        pressure: '100',
        dayTemp: '0',
        nightTemp: '0',
        mornTemp: '0',
        eveTemp: '0',
        minTemp: '0',
        maxTemp: '0',
      }
    ]
  }

];

function App() {
  const forecastList = useForecastList();
  const [degUnit, handleDegC, handleDegF] = useDeg();
  const [open, handleOpen, handleClose, wallpaper, setWallpaper] = useOptions();

  return (
    <div class='h-screen break-words flex flex-col' style={{ overflow: 'hidden', backgroundImage: `url(${wallpaper})`, backgroundSize: 'cover' }}>
      <NavBar handleOpen={handleOpen} />
      <div class="overflow-x-auto">
        <Dashboard degUnit={degUnit} forecastList={forecastList} />
        <Forecast degUnit={degUnit} data={forecastList.selectedForecast} />
        <Options open={open} wallpaper={wallpaper} setWallPaper={setWallpaper} handleClose={handleClose} handleDegC={handleDegC} handleDegF={handleDegF} hidden />
      </div>
    </div>
  );
}

export default App;
